import React from 'react';
import styled from 'styled-components';
import './View-main.css';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import {useState} from 'react';
import Data from '../data/ZX10R-m.json';

function ZX10R_main() {

  const [burgerStatus, setBurgerStatus] = useState(false);
  const [imgName, setImgName] = useState('/images/ZX10R-view-1.png')

  function b1(e){
    setImgName('/images/ZX10R-view-1.png')
  }
  function b2(e){
    setImgName('/images/ZX10R-view-2.png')
  }

  return (
      <Container>
        
        <Top id="top">
          <a>
            <img id="img" src='/images/Kawasaki-logo-2.png' alt=""/>
          </a>  

          <Menu>
            <a id="t-a" href="/Ninja-ZX10R/view-main">About Ninja-ZX10R</a>
            <a id="t-a" href="/Ninja-ZX10R/view-feature">Features</a>
            <a id="t-a" href="/Ninja-ZX10R/view-specification">Specification</a>
            <a id="t-a" href="/Ninja-ZX10R">Back</a>
          </Menu>

          <CustomMenu onClick={() => setBurgerStatus(true)}/>

          <BurgerNav show={burgerStatus}>
            <CustomWrapper>
              <CustomClose onClick={() => setBurgerStatus(false)}/>
            </CustomWrapper>
            <li><a id="t-a1" href="/Ninja-ZX10R/view-main">About Ninja-ZX10R</a></li>
            <li><a id="t-a1" href="/Ninja-ZX10R/view-feature">Features</a></li>
            <li><a id="t-a1" href="/Ninja-ZX10R/view-specification">Specification</a></li>
            <li><a id="t-a1" href="/Ninja-ZX10R">Back</a></li>
          </BurgerNav>
        </Top>

        <Left id="left">
          <img id="img-m" src={imgName}/>
        </Left>

        {Data.map((val,key) =>{
                return(
          <Right id="right"  key={key}>
              <h1 id="h1-1">{val.Name}</h1>
              <p id="p-1">{val.Bharat_Stage}</p>
              <h3 id="h3-1">{val.Price}</h3>
              <p id="p-1">{val.Tag}</p>
              <a href="/Location-Enquire-ZX10R">On-Road Price Estimation</a>
            <button id="btn-ZX10R-1" onClick={() => {b1();}}>Candy Lime Green</button>
            <button id="btn-ZX10R-2" onClick={() => {b2();}}>Metallic Spark Black</button>
            </Right>
        )
      })}

        <Bottom>
          <a href="/Ninja-ZX10R/view-feature">
            <DownArrow src='/images/Down-Arrow-1.png'></DownArrow>
          </a>
          <a href="/Location">
            <button id="btn-e">Enquire</button>
          </a>
        </Bottom>

      </Container>
  );
}

export default ZX10R_main;


//--Others--//

const Left = styled.div`
    
    display:inline-block;
    float:left;
    @media(max-width:768px){
      width: 65%; 
      height: 72%;
      #img-m{
        width:80%;
      }
    }
    @media(min-width:768px){
      width: 75%; 
      height: 72%;
    }
`
const Right = styled.div`
    
    display:inline-block;
    @media(max-width:768px){
      width: 35%; 
      height: 72%;
    }
    @media(min-width:768px){
      width: 25%; 
      height: 72%;
    }
`

const Top = styled.div`
    width: 100%; 
    height: 12%;
    
`

const Bottom = styled.div`
    width: 100%; 
    height: 17%;
`

const Container = styled.div`
  height:100vh;
  background-image: linear-gradient(lime,grey,black);
`

const Menu = styled.div`
font-weight: 600;
  @media(max-width:768px){
    display:none
  }
`

const CustomMenu = styled(MenuIcon)`
  cursor: pointer;
  margin-top: 10px;
`

const BurgerNav = styled.div`
  position:fixed;
  top:0;
  bottom:0;
  right:0;
  background: lime;
  width:H2Rpx;
  font-weight: 600;
  list-style:none;
  padding: 20px;
  display:flex;
  flex-direction: column;
  text-align: start;
  transform: ${props => props.show ? 'translateX(0)': 'translateX(100%)' };
  transition: transform 0.3s ease-in-out;
  li{
    padding 15px 0;
    border-bottom: 1px solid white;

  }
`

const CustomClose = styled(CloseIcon)`
  cursor:pointer;
`

const CustomWrapper = styled.div`
  display:flex;
  justify-content: flex-end;
`

const DownArrow = styled.img`
    height: 40px;
    overflow-x: hidden;
    animation: animateDown infinite 1.5s;   
`