import React from 'react';
import styled from 'styled-components';
import '../location/Location.css'
import { Scrollbars } from 'react-custom-scrollbars';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import {useState} from 'react';
import Location_Data_N650 from '../data/Location_N650.json';

function TR_N650() {

  const [burgerStatus, setBurgerStatus] = useState(false);
  const [searchTerm, setSearchTerm] = useState('')

  return (
    <Container>
      
        <Top id="top">
          <a>
            <img id="img" src='/images/Kawasaki-logo-2.png' alt=""/>
          </a>  
          
          <Menu>
            <a id="t-a" href="/Ninja-650">Back</a>
            <a id="t-a" href="/Contact-us">Contact us</a>
          </Menu>

          <CustomMenu onClick={() => setBurgerStatus(true)}/>

          <BurgerNav show={burgerStatus}>
            <CustomWrapper>
              <CustomClose onClick={() => setBurgerStatus(false)}/>
            </CustomWrapper>
            <li><a id="t-a1" href="/Ninja-650">Back</a></li>
            <li><a id="t-a1" href="/Contact-us">Contact us</a></li>
          </BurgerNav>
        </Top>

      <Upper_Middle id="up-mid">\
          <h1>Ninja 650 Available Location for Test Drive</h1>
          <input id="in-l" 
            type="text" 
            placeholder='Search by location.....'
            onChange={e => { setSearchTerm(e.target.value)}}
          />
      </Upper_Middle>

      <Middle>
      <Scrollbars style={{ width: '100%', height: '100%' }}>
        <div id="div-l-m">
          {Location_Data_N650.filter((val) => {
            if( searchTerm == ""){
              return val
            }
            else if(val.Location.toLocaleLowerCase().includes(searchTerm.toLocaleLowerCase())){
              return val
            }
          }).map((val, key) =>{
            return (
              <div id="div-l-sub" key={key}>
                <h1 id="loc-1" >{val.ShowroomName}</h1>
                <h4 id="loc-2" >{val.Location}</h4>
                <h5 id="loc-3" >{val.E_Mail}</h5>
                <h3 id="loc-4" >{val.Contact}</h3>
                <h2 id="loc-5" >{val.State}</h2>
                <a  id="loc-6" href={val.Map}>Map</a>
              </div>
            );
          })}

        </div>
      </Scrollbars>
      </Middle>

    </Container>
  );
}

export default TR_N650;



//--Others--//

const Top = styled.div`
    width: 100%; 
    height: 10%;
`

const Upper_Middle = styled.div`
    width: 100%; 
    height: 20%;
`

const Middle = styled.div`
    width: 100%; 
    height: 70%;
`

const Container = styled.div`
  height:100vh;
  z-index:-1;
  background-image: url('/images/Location-m-big.png');
  background-repeat: no-repeat;
  background-size: cover;
`

const Menu = styled.div`
font-weight: 600;
  @media(max-width:768px){
    display:none
  }
`

const CustomMenu = styled(MenuIcon)`
  cursor: pointer;
  margin-top: 10px;
`

const BurgerNav = styled.div`
  position:fixed;
  top:0;
  bottom:0;
  right:0;
  background: lime;
  width:300px;
  font-weight: 600;
  list-style:none;
  padding: 20px;
  display:flex;
  flex-direction: column;
  text-align: start;
  transform: ${props => props.show ? 'translateX(0)': 'translateX(100%)' };
  transition: transform 0.3s ease-in-out;
  li{
    padding 15px 0;
    border-bottom: 1px solid white;

  }
  z-index:1;
`

const CustomClose = styled(CloseIcon)`
  cursor:pointer;
`

const CustomWrapper = styled.div`
  display:flex;
  justify-content: flex-end;
`