import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    bikes: [
        "Ninja-300", 
        "Ninja-H2R",
        "Ninja-Z900",  
        "Ninja-ZX10R",  
        "Ninja-650",
        "Riding-Gears"
    ]
}

const bikeSlice = createSlice({
    name:"bike",
    initialState,
    reducers: {}
})

export const selectBikes = state => state.bike.bikes;

export default bikeSlice.reducer;