import React, { Component } from 'react';
import './Front.css';
import styled from 'styled-components';
import { useHistory } from "react-router-dom";


function Front() {
  const link = "/video/video-m-big-2.mp4";
  var audio1 = new Audio("/sound/sound-2.mp3")
  let history = useHistory();

  function b(){
    for(let i=1; i<=900000; i++){
      audio1.play();
      }
    history.push("/Ninja-300");
  }

  function s(){
    for(let i=1; i<=900000; i++){
      audio1.play();
      }
    history.push("/Shop");
  }

  function l(){
    for(let i=1; i<=900000; i++){
      audio1.play();
      }
    history.push("/Location");
  }

  return (
    <Container>

        <Video>
        <video autoPlay loop muted id='video'>
          <source src={link} type="video/mp4"/>
        </video>
        </Video>

        <Main id="main-f">
          <h1 id="title">Welcome To Kawasaki</h1>
          <div id="div-f">
            <button onClick={b} id="btn-f">View Bikes</button>
            <button onClick={s} id="btn-f">Shop</button>
            <button onClick={l} id="btn-f">Location</button>
          </div>
        </Main>

    </Container>
  )
}

export default Front;


//--Others--//

const Video = styled.div`
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index:-1;
`

const Main = styled.div`
  width: 100vw;
  height: 100vh;
  z-index:1;
`

const Container = styled.div`
  height:100vh;
`