import React from 'react';
import {useState} from 'react';
import './Header.css';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import styled from 'styled-components';
import './Section.style.css';
import { selectBikes } from '../features/bike/bikeSlice'
import { useSelector } from 'react-redux';

function Header() {

  const [burgerStatus, setBurgerStatus] = useState(false);
  const bikes = useSelector(selectBikes)

  var audio = new Audio("/sound/sound-2.mp3")

  function sound(e){
    for(let i=1; i<=700000; i++){
      audio.play();
    }
  }

  return (
    <Container>
      <a>
        <img id="img" src='/images/Kawasaki-logo-2.png' alt=""/>
      </a>

      <Menu>
        {bikes && bikes.map((bike, index)=>(
            <a key={index} id="a1" href={`/${bike}`}  onClick={sound}>{bike}</a>  
        ))} 
      </Menu>

      <RightMenu>
        <a id="a2" onClick={sound} href="/Shop">Shop</a>
        <a id="a2" onClick={sound} href="/Location">Location</a>
        <CustomMenu onClick={() => setBurgerStatus(true)}/>
      </RightMenu>

      <BurgerNav show={burgerStatus}>
        <CustomWrapper>
          <CustomClose onClick={() => setBurgerStatus(false)}/>
        </CustomWrapper>
          {bikes && bikes.map((bike, index)=>(
              <li key={index}><a onClick={sound} id="a1" href={`/${bike}`}>{bike}</a></li>
          ))} 
          <li><a onClick={sound} href="/Login">Login</a></li>
          <li><a onClick={sound} href="/Location">Location</a></li>
          <li><a onClick={sound} href="/Test_Ride">Test Drive</a></li>
          <li><a onClick={sound} href="/Contact-us">Contact us</a></li>
          <p id="li-c">Creator : Naveen</p>
      </BurgerNav>

    </Container>
  );
}

export default Header;

//--Others--//

const Container = styled.div`
  width: 100vw;
  min-height:60px;
  position: fixed;
  display: flex;
  align-items: center;
  padding 0 20px;
  justify-content: space-between;
  z-index:1;
`

const Menu = styled.div`
  display: flex;
  align-items:center;  
  justify-content: center;
  flex:1;

  #a1{
    font-weight: 600;
    text-transform: uppercase;
    padding: 0 10px;
    flex-warp: no-warp;
  }
  @media(max-width:768px){
    display:none
  }
`

const RightMenu = styled.div`
display: flex;
align-items: centre;
#a2{
  font-weight: 600;
  text-transform: uppercase;
  margin-right: 10px;
}
`

const CustomMenu = styled(MenuIcon)`
  cursor: pointer;
  margin-top:-4.8px;

`

const BurgerNav = styled.div`
  position:fixed;
  top:0;
  bottom:0;
  right:0;
  background: lime;
  width:300px;
  font-weight: 600;
  list-style:none;
  padding: 20px;
  display:flex;
  flex-direction: column;
  text-align: start;
  transform: ${props => props.show ? 'translateX(0)': 'translateX(100%)' };
  transition: transform 0.3s ease-in-out;
  li{
    padding 15px 0;
    border-bottom: 1px solid white;

  }
`

const CustomClose = styled(CloseIcon)`
  cursor:pointer;
`

const CustomWrapper = styled.div`
  display:flex;
  justify-content: flex-end;
`