import React from 'react';
import '../estimation/Estimation.css';
import { Table } from 'react-bootstrap';
import styled from 'styled-components';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import { Scrollbars } from 'react-custom-scrollbars';
import {useState} from 'react';

function Estimation_Z900(props) {

  const [burgerStatus, setBurgerStatus] = useState(false);
  const total= props.tax+props.ex+props.rto+props.insurance+props.others
  const state={
    curTime : new Date().toLocaleString(),
  }

  return (
    <Container>

        <Top id="top">
          <a>
            <img id="img" src='/images/Kawasaki-logo-2.png' alt=""/>
          </a>  

          <Menu>
            <a id="t-a" href="/Ninja-Z900/view-main">About Ninja-H2R</a>
            <a id="t-a" href="/Ninja-Z900/view-feature">Features</a>
            <a id="t-a" href="/Ninja-Z900/view-specification">Specification</a>
            <a id="t-a" href="/Ninja-Z900">Back</a>
          </Menu>

          <CustomMenu onClick={() => setBurgerStatus(true)}/>

          <BurgerNav show={burgerStatus}>
            <CustomWrapper>
              <CustomClose onClick={() => setBurgerStatus(false)}/>
            </CustomWrapper>
            <li><a id="t-a1" href="/Ninja-Z900/view-main">About Ninja-H2R</a></li>
            <li><a id="t-a1" href="/Ninja-Z900/view-feature">Features</a></li>
            <li><a id="t-a1" href="/Ninja-Z900/view-specification">Specification</a></li>
            <li><a id="t-a1" href="/Ninja-Z900">Back</a></li>
          </BurgerNav>
        </Top>

        <Middle>
          <Scrollbars style={{ width: '100%', height: '100%' }}>
          <div id="div-e1">
            <h1 id="h1-e">{props.ShowroomName}</h1>
            <h2 id="h2-e">Kawasaki Z900</h2>
            <h3 id="h3-e1">{state.curTime}</h3>
            <h3 id="h3-e2">{props.mail}</h3>
            <h3 id="h3-e2">{props.contact}</h3>
            <div id="div-e">

              <Table striped bordered hover>
                  <tbody>
                    <tr>
                      <td width="20%">Ex-Showroom Price</td>
                      <td width="20%">₹{props.ex}</td>
                    </tr>
                    <tr>
                      <td width="20%">Rto</td>
                      <td width="20%">₹{props.rto}</td>
                    </tr>
                    <tr>
                      <td width="20%">Tax [LifeTime]</td>
                      <td width="20%">₹{props.tax}</td>
                    </tr>
                    <tr>
                      <td width="20%">Insurance [1 year- OD + 5 year- 3rd Party]</td>
                      <td width="20%">₹{props.insurance}</td>
                    </tr>
                    <tr>
                      <td width="20%">Extra Fittings</td>
                      <td width="20%">₹{props.others}</td>
                    </tr>
                    <tr>
                      <td width="20%">Total</td>
                      <td width="20%">₹{total}</td>
                    </tr>
                  </tbody>  
              </Table>

            </div>
          </div>
          </Scrollbars>
        </Middle>

        <Bottom>
        <p id="p-e">@{new Date().getFullYear()} INDIA KAWASAKI MOTORS PVT. LTD.</p>
        </Bottom>

    </Container>
  )
}

export default Estimation_Z900;


//--Others--//


const Top = styled.div`
    width: 100%; 
    height: 10%;
    background: red;
`

const Middle = styled.div`
    width: 100%; 
    height: 80%;
    background: wheat;
`

const Bottom = styled.div`
    width: 100%; 
    height: 10%;
    background: black;
`

const Container = styled.div`
    height:100vh;
    z-index: -1;
`

const Menu = styled.div`
    font-weight: 600;
    @media(max-width:768px){
    display:none
    }
`

const CustomMenu = styled(MenuIcon)`
  cursor: pointer;
  margin-top: 10px;
`

const BurgerNav = styled.div`
    font-weight: 600;
    position:fixed;
    top:0;
    bottom:0;
    right:0;
    background: lime;
    width:300px;
    list-style:none;
    padding: 20px;
    display:flex;
    flex-direction: column;
    text-align: start;
    transform: ${props => props.show ? 'translateX(0)': 'translateX(100%)' };
    transition: transform 0.3s ease-in-out;
    z-index: 1;
    li{
      padding 15px 0;
      border-bottom: 1px solid white;
  
    }
`


const CustomClose = styled(CloseIcon)`
  cursor:pointer;
`

const CustomWrapper = styled.div`
  display:flex;
  justify-content: flex-end;
`