import Sports_clothing from '../clothing-main/Sports-Clothing';
import T_Shirt_clothing from '../clothing-main/T-Shirt-Clothing';
import WSBK_clothing from '../clothing-main/WSBK-Clothing';
import Contact_us from '../contact-us/Contact-us';
import Feedback from '../contact-us/Feedback';
