import RG_c_json from '../data/RG-c.json';
import Sports_c_json from '../data/Sports-c.json';
import WSBK_c_json from '../data/WSBK-c.json';
import T_Shirt_c_json from '../data/T-Shirt-c.json';
import Product_view_a_H2R from '../product view/Product-view-a-H2R';
import Product_view_a_N300 from '../product view/Product-view-a-N300';
import Product_view_a_Z900 from '../product view/Product-view-a-Z900';
import Product_view_a_ZX10R from '../product view/Product-view-a-ZX10R';
import Product_view_a_N650 from '../product view/Product-view-a-N650';
import Product_view_c_RG from '../product view/Product-view-c-RG';
import Product_view_c_Sports from '../product view/Product-view-c-Sports';
import Product_view_c_WSBK from '../product view/Product-view-c-WSBK';
import Product_view_c_Shirt from '../product view/Product-view-c-T-Shirt';
import Location_Enquire_N300 from '../estimation/Location-Enquire-N300';
import Estimation_N300 from '../estimation/Estimation-N300';
import Location_Data_N300 from '../data/Location_N300.json';
import Location_Enquire_H2R from '../estimation/Location-Enquire-H2R';
import Estimation_H2R from '../estimation/Estimation-H2R';
import Location_Data_H2R from '../data/Location_H2R.json';
import Location_Enquire_N650 from '../estimation/Location-Enquire-N650';
import Estimation_N650 from '../estimation/Estimation-N650';
import Location_Data_N650 from '../data/Location_N650.json';
import Location_Enquire_Z900 from '../estimation/Location-Enquire-Z900';
import Estimation_Z900 from '../estimation/Estimation-Z900';
import Location_Data_Z900 from '../data/Location_Z900.json';
import Location_Enquire_ZX10R from '../estimation/Location-Enquire-ZX10R';
import Estimation_ZX10R from '../estimation/Estimation-ZX10R';
import Location_Data_ZX10R from '../data/Location_ZX10R.json';

function Home() {

  return (
    <Container>
      <Router>
        <Switch>

        {H2R_a_json.map((val,key) =>{
          return(
          <Route key={key} path={"/Ninja-H2R/accessory-main/Ninja-H2R-a-item"+key}>
              <Product_view_a_H2R title={val.item} image={val.imgage} description={val.description}/>
          </Route>
          )
        })}

        {N300_a_json.map((val,key) =>{
          return(
          <Route key={key} path={"/Ninja-300/accessory-main/Ninja-300-a-item"+key}>
            <Product_view_a_N300 title={val.item} image={val.imgage} description={val.description}/>
          </Route>
          )
        })}

        {Z900_a_json.map((val,key) =>{
          return(
          <Route key={key} path={"/Ninja-Z900/accessory-main/Ninja-Z900-a-item"+key}>
              <Product_view_a_Z900 title={val.item} image={val.imgage} description={val.description}/>
          </Route>
          )
        })}

        {ZX10R_a_json.map((val,key) =>{
          return(
          <Route key={key} path={"/Ninja-ZX10R/accessory-main/Ninja-ZX10R-a-item"+key}>
              <Product_view_a_ZX10R title={val.item} image={val.imgage} description={val.description}/>
          </Route>
          )
        })}

        {N650_a_json.map((val,key) =>{
          return(
          <Route key={key} path={"/Ninja-650/accessory-main/Ninja-650-a-item"+key}>
              <Product_view_a_N650 title={val.item} image={val.imgage} description={val.description}/>
          </Route>
          )
        })}

        {RG_c_json.map((val,key) =>{
          return(
          <Route key={key} path={"/Riding-Gears/accessory-main/Riding-Gears-a-item"+key}>
              <Product_view_c_RG title={val.item} image={val.imgage} description={val.description}/>
          </Route>
          )
        })}

        {Sports_c_json.map((val,key) =>{
          return(
          <Route key={key} path={"/Shop/sports-clothing/Sports-c-item"+key}>
              <Product_view_c_Sports title={val.item} image={val.imgage} description={val.description}/>
          </Route>
          )
        })}

        
        {WSBK_c_json.map((val,key) =>{
          return(
          <Route key={key} path={"/Shop/wsbk-clothing/WSBK-c-item"+key}>
              <Product_view_c_WSBK title={val.item} image={val.imgage} description={val.description}/>  
          </Route>
          )
        })}

        {T_Shirt_c_json.map((val,key) =>{
          return(
          <Route key={key} path={"/Shop/t-shirts-clothing/Shirt-c-item"+key}>
            <Product_view_c_Shirt title={val.item} image={val.imgage} description={val.description}/> 
          </Route>
          )
        })}


        {Location_Data_N300.map((val,key) =>{
          return(
          <Route key={key} path={"/Location-Enquire-N300/Estimation-N300-"+val.ShowroomName}>
            <Estimation_N300 ShowroomName={val.ShowroomName} mail={val.E_Mail} contact={val.Contact} ex={val.Ex_Showroom} rto={val.Rto} tax={val.Tax} insurance={val.Insurance} others={val.Others}/>
          </Route>
          )
        })}


        {Location_Data_H2R.map((val,key) =>{
          return(
          <Route key={key} path={"/Location-Enquire-H2R/Estimation-H2R-"+val.ShowroomName}>
            <Estimation_H2R ShowroomName={val.ShowroomName} mail={val.E_Mail} contact={val.Contact} ex={val.Ex_Showroom} rto={val.Rto} tax={val.Tax} insurance={val.Insurance} others={val.Others}/>
          </Route>
          )
        })}

        {Location_Data_Z900.map((val,key) =>{
          return(
          <Route key={key} path={"/Location-Enquire-Z900/Estimation-Z900-"+val.ShowroomName}>
            <Estimation_Z900 ShowroomName={val.ShowroomName} mail={val.E_Mail} contact={val.Contact} ex={val.Ex_Showroom} rto={val.Rto} tax={val.Tax} insurance={val.Insurance} others={val.Others}/>
          </Route>
          )
        })}

        {Location_Data_N650.map((val,key) =>{
          return(
          <Route key={key} path={"/Location-Enquire-N650/Estimation-N650-"+val.ShowroomName}>
            <Estimation_N650 ShowroomName={val.ShowroomName} mail={val.E_Mail} contact={val.Contact} ex={val.Ex_Showroom} rto={val.Rto} tax={val.Tax} insurance={val.Insurance} others={val.Others}/>
          </Route>
          )
        })}

        {Location_Data_ZX10R.map((val,key) =>{
          return(
          <Route key={key} path={"/Location-Enquire-ZX10R/Estimation-ZX10R-"+val.ShowroomName}>
            <Estimation_ZX10R ShowroomName={val.ShowroomName} mail={val.E_Mail} contact={val.Contact} ex={val.Ex_Showroom} rto={val.Rto} tax={val.Tax} insurance={val.Insurance} others={val.Others}/>
          </Route>
          )
        })}

          <Route path="/Test-Ride/Location-N300:">
            <TR_N300/>
          </Route>

          <Route path="/Test-Ride/Location-H2R">
            <TR_H2R/>
          </Route>

          <Route path="/Test-Ride/Location-N650">
            <TR_N650/>
          </Route>

          <Route path="/Test-Ride/Location-Z900">
            <TR_Z900/>
          </Route>

          <Route path="/Test-Ride/Location-ZX10R">
            <TR_ZX10R/>
          </Route>

          <Route path="/Contact-us/Feedback">
            <Feedback/>
          </Route>

          <Route path="/Shop/sports-clothing">
            <Sports_clothing/>
          </Route>

          <Route path="/Shop/t-shirts-clothing">
            <T_Shirt_clothing/>
          </Route>

          <Route path="/Shop/wsbk-clothing">
            <WSBK_clothing/>
          </Route>

          <Route path="/Ninja-300/view-main">
            <N300_main/>
          </Route>

          <Route path="/Ninja-300/view-feature">
            <N300_feature/>
          </Route>

          <Route path="/Ninja-300/view-specification">
            <N300_specification/>
          </Route>

          <Route path="/Ninja-H2R/view-main">
            <H2R_main/>
          </Route>

          <Route path="/Ninja-H2R/view-feature">
            <H2R_feature/>
          </Route>

          <Route path="/Ninja-H2R/view-specification">
            <H2R_specification/>
          </Route>

          <Route path="/Ninja-Z900/view-main">
            <Z900_main/>
          </Route>

          <Route path="/Ninja-Z900/view-feature">
            <Z900_feature/>
          </Route>

          <Route path="/Ninja-Z900/view-specification">
            <Z900_specification/>
          </Route>

          <Route path="/Ninja-ZX10R/view-main">
            <ZX10R_main/>
          </Route>

          <Route path="/Ninja-ZX10R/view-feature">
            <ZX10R_feature/>
          </Route>

          <Route path="/Ninja-ZX10R/view-specification">
            <ZX10R_specification/>
          </Route>

          <Route path="/Ninja-650/view-main">
            <N650_main/>
          </Route>

          <Route path="/Ninja-650/view-feature">
            <N650_feature/>
          </Route>

          <Route path="/Ninja-650/view-specification">
            <N650_specification/>
          </Route>

          <Route path="/Ninja-300/accessory-main">
            <N300_accessory_main/>
          </Route>

          <Route path="/Ninja-H2R/accessory-main">
            <H2R_accessory_main/>
          </Route>

          <Route path="/Ninja-Z900/accessory-main">
            <Z900_accessory_main/>
          </Route>

          <Route path="/Ninja-ZX10R/accessory-main">
            <ZX10R_accessory_main/>
          </Route>

          <Route path="/Ninja-650/accessory-main">
            <N650_accessory_main/>
          </Route>

          <Route path="/Riding-Gears/clothing-main">
            <RG_clothing_main/>
          </Route>

          <Route path="/Location-Enquire-N300">
            <Location_Enquire_N300/>
          </Route>

          <Route path="/Location-Enquire-N650">
            <Location_Enquire_N650/>
          </Route>

          <Route path="/Location-Enquire-H2R">
            <Location_Enquire_H2R/>
          </Route>

          <Route path="/Location-Enquire-Z900">
            <Location_Enquire_Z900/>
          </Route>

          <Route path="/Location-Enquire-ZX10R">
            <Location_Enquire_ZX10R/>
          </Route>

          <Route path="/Test_Ride">
            <Test_Ride/>
          </Route>

          <Route path="/Login">
            <Login/>
          </Route>

          <Route path="/Shop">
            <Shop/>
          </Route>

          <Route path="/Location">
            <Location/>
          </Route>

          <Route path="/Contact-us">
            <Contact_us/>
          </Route>

          <Route path="/Ninja-H2R">
          <Header/>
            <Section_H2R
              title={"Ninja H2R"}
              description={"It's Time to Grab your Dream Bike"}
              leftBtnText={"View"}
              rightBtnText={"Accessory"}
            />
          </Route>
          
          <Route path="/Ninja-Z900">
          <Header/>
            <Section_Z900
              title={"Ninja Z900"}
              description={"It's Time to Grab your Dream Bike"}
              leftBtnText={"View"}
              rightBtnText={"Accessory"}
            />
          </Route>
          
          <Route path="/Ninja-ZX10R">
          <Header/>
            <Section_ZX10R
              title={"Ninja ZX10R"}
              description={"It's Time to Grab your Dream Bike"}
              leftBtnText={"View"}
              rightBtnText={"Accessory"}
            />
          </Route>
          
          <Route path="/Ninja-650">
          <Header/>
            <Section_N650
              title={"Ninja N650"}
              description={"It's Time to Grab your Dream Bike"}
              leftBtnText={"View"}
              rightBtnText={"Accessory"}
            />
          </Route>
          
          <Route path="/Riding-Gears">
          <Header/>
            <Section_RG
              title={"Perfect Gears"}
              description={"We Ensure Your Saftey"}
              leftBtnText={"View"}
              rightBtnText=""
            />
          </Route>
          
          <Route path="/Ninja-300">
          <Header/>
            <Section_N300
              title={"Ninja 300"}
              description={"It's Time to Grab your Dream Bike"}
              leftBtnText={"View"}
              rightBtnText={"Accessory"}
            />
          </Route>

          <Route exact path='/'>
            <Front/>
          </Route>
    
        </Switch>
        </Router>

    </Container>
  );
}
export default Home;

//--Others--//

const Container = styled.div`
    height: 100vh;
`