import React from 'react';
import styled from 'styled-components';
import './Accessory.css'
import { Scrollbars } from 'react-custom-scrollbars';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import {useState} from 'react';
import Data from '../data/ZX10R-a.json';
import { useHistory } from 'react-router-dom';

function ZX10R_accessory_main() {

  const [burgerStatus, setBurgerStatus] = useState(false);
  const history = useHistory();

  function item(...args){
    history.push("/Ninja-ZX10R/accessory-main/Ninja-ZX10R-a-item"+args);
  }
  return (
    <Container id ="cont-a">
      
        <Top id="top">
          <a>
            <img id="img" src='/images/Kawasaki-logo-2.png' alt=""/>
          </a>  
          
          <Menu>
            <a id="t-a" href="/Ninja-ZX10R">Back</a>
            <a id="t-a" href="/Shop">Back to Shop</a>
            <a id="t-a" href="/Contact-us">Contact us</a>
          </Menu>

          <CustomMenu onClick={() => setBurgerStatus(true)}/>

          <BurgerNav show={burgerStatus}>
            <CustomWrapper>
              <CustomClose onClick={() => setBurgerStatus(false)}/>
            </CustomWrapper>
            <li><a id="t-a1" href="/Ninja-ZX10R">Back</a></li>
            <li><a id="t-a1" href="/Shop">Back to Shop</a></li>
            <li><a id="t-a1" href="/Contact-us">Contact us</a></li>
          </BurgerNav>
        </Top>

      <Middle>
      <Scrollbars style={{ width: '100%', height: '100%' }}>
        <div id="div-a-m">
          {Data.map((val,key) =>{
            return(
            <div id="div-a-sub" key={key}>
                <img id="img-a-m" src={val.imgage}/>
                <h1 id="h1-a-1">{val.item}</h1>
                <button onClick={() =>  {item(key)}} id="btn-1">View</button>
            </div>
            )
          })}
        </div>
      </Scrollbars>
      </Middle>

    </Container>
  );
}

export default ZX10R_accessory_main;



//--Others--//

const Top = styled.div`
    width: 100%; 
    height: 10%;
    background: brown;
`

const Middle = styled.div`
    width: 100%; 
    height: 90%;
`

const Container = styled.div`
  height:100vh;
  z-index:-1;
`

const Menu = styled.div`
font-weight: 600;
  @media(max-width:768px){
    display:none
  }
`

const CustomMenu = styled(MenuIcon)`
  cursor: pointer;
  margin-top: 10px;
`

const BurgerNav = styled.div`
  position:fixed;
  top:0;
  bottom:0;
  right:0;
  background: lime;
  width:300px;
  font-weight: 600;
  list-style:none;
  padding: 20px;
  display:flex;
  flex-direction: column;
  text-align: start;
  transform: ${props => props.show ? 'translateX(0)': 'translateX(100%)' };
  transition: transform 0.3s ease-in-out;
  li{
    padding 15px 0;
    border-bottom: 1px solid white;

  }
  z-index:1;
`

const CustomClose = styled(CloseIcon)`
  cursor:pointer;
`

const CustomWrapper = styled.div`
  display:flex;
  justify-content: flex-end;
`