import React from 'react';
import styled from 'styled-components';
import './View-feature.css';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import { Scrollbars } from 'react-custom-scrollbars';
import Bounce from 'react-reveal/Bounce';
import {useState} from 'react';
import Data from '../data/N650-f.json';

function N300_feature() {

  const [burgerStatus, setBurgerStatus] = useState(false);

  return (
    <Container>

        <Top id="top">
          <a>
            <img id="img" src='/images/Kawasaki-logo-2.png' alt=""/>
          </a>  

          <Menu>
            <a id="t-a" href="/Ninja-650/view-main">About Ninja-650</a>
            <a id="t-a" href="/Ninja-650/view-feature">Features</a>
            <a id="t-a" href="/Ninja-650/view-specification">Specification</a>
            <a id="t-a" href="/Ninja-650">Back</a>
          </Menu>

          <CustomMenu onClick={() => setBurgerStatus(true)}/>

          <BurgerNav show={burgerStatus}>
            <CustomWrapper>
              <CustomClose onClick={() => setBurgerStatus(false)}/>
            </CustomWrapper>
            <li><a id="t-a1" href="/Ninja-650/view-main">About Ninja-650</a></li>
            <li><a id="t-a1" href="/Ninja-650/view-feature">Features</a></li>
            <li><a id="t-a1" href="/Ninja-650/view-specification">Specification</a></li>
            <li><a id="t-a1" href="/Ninja-650">Back</a></li>
          </BurgerNav>
        </Top>

        <Middle>
        <Middle_mid id="mid">
        <Scrollbars style={{ width: '100%', height: '100%' }}>

          {Data.map((val,key) =>{
                  return(

              <div id="div1" key={key}>
                <Bounce>
                  <img id="img-f" src={val.image}/>
                </Bounce>
                  <h1 id="h1-f">{val.title}</h1>
                  <p id="p1">{val.description}</p>
              </div>
              )
          })}

        </Scrollbars>
        </Middle_mid>
        </Middle>

        <Bottom>
          <a href="/Ninja-650/view-specification">
            <DownArrow src='/images/Down-Arrow-1.png'></DownArrow>
          </a>
        </Bottom>

    </Container>
  );
}

export default N300_feature


//--Others--//

const Top = styled.div`
    width: 100%; 
    height: 10%;
`

const Middle = styled.div`
    width: 100%; 
    height: 80%;
`

const Middle_mid = styled.div`
    width: 70%; 
    height: 100%;
`

const Bottom = styled.div`
    width: 100%; 
    height: 10%;
`

const Container = styled.div`
  height:100vh;
  z-index: -1;
  background:black;
  @media (min-width: 768px){
    background-image: url('/images/Contact-us-m-big.png');
  }
  @media (max-width: 768px){
    background-image: url('/images/Contact-us-m-small1.png');
  }
  background-repeat: no-repeat;
  background-size: cover;
`

const Menu = styled.div`
font-weight: 600;
  @media(max-width:768px){
    display:none
  }
`

const CustomMenu = styled(MenuIcon)`
  cursor: pointer;
  margin-top: 10px;
`

const BurgerNav = styled.div`
  position:fixed;
  top:0;
  bottom:0;
  right:0;
  background: lime;
  width:300px;
  font-weight: 600;
  list-style:none;
  padding: 20px;
  display:flex;
  flex-direction: column;
  text-align: start;
  transform: ${props => props.show ? 'translateX(0)': 'translateX(100%)' };
  transition: transform 0.3s ease-in-out;
  z-index: 1;
  li{
    padding 15px 0;
    border-bottom: 1px solid white;

  }
`

const CustomClose = styled(CloseIcon)`
  cursor:pointer;
`

const CustomWrapper = styled.div`
  display:flex;
  justify-content: flex-end;
`

const DownArrow = styled.img`
    height: 40px;
    overflow-x: hidden;
    animation: animateDown infinite 1.5s;   
`