import React from 'react'
import styled from 'styled-components';
import './Contact-us.css'
import { Scrollbars } from 'react-custom-scrollbars';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import {useState} from 'react';

function Contact_us() {

    const [burgerStatus, setBurgerStatus] = useState(false);

  return (
    <Container>

        <Top id="top">
          <a>
            <img id="img" src='/images/Kawasaki-logo-2.png' alt=""/>
          </a>  
          
          <Menu>
            <a  id="t-a" href="/Ninja-300">Back</a>
          </Menu>

          <CustomMenu onClick={() => setBurgerStatus(true)}/>

          <BurgerNav show={burgerStatus}>
            <CustomWrapper>
              <CustomClose onClick={() => setBurgerStatus(false)}/>
            </CustomWrapper>
            <li><a id="t-a1" href="/Ninja-300">Back</a></li>
          </BurgerNav>
        </Top>

        <Middle>
        <Middle_mid id="mid">
        <Scrollbars style={{ width: '100%', height: '100%' }}>
          <h1>India Kawasaki Motors Pvt. Ltd.</h1>
          <img id="img-co" src="/images/call.png"></img>
          <h2>Toll Free No.</h2>
          <h2>1800 266 0365</h2>
          <h4>Monday To Saturday 9 AM to 6 PM</h4>
          <img id="img-co" src="/images/mail.png"></img>
          <h2>info@kawasaki-india.com</h2>
          <a id="a-co" href="/Contact-us/Feedback">Feedback</a>
        </Scrollbars>
        </Middle_mid>
        </Middle>

        <Bottom id="btm-co">
          <a href="https://play.google.com/store/apps/details?id=com.ikmconnect.app">
            <img src='/images/googleplay.png'/>
          </a>

          <p id="p-co">@{new Date().getFullYear()} INDIA KAWASAKI MOTORS PVT. LTD.</p>

          <a href="https://apps.apple.com/in/app/ikm-connect/id1415300264">
            <img src='/images/i-tune.png'/>
          </a>
        </Bottom>

    </Container>
  )
}

export default Contact_us


//--Others--//

const Top = styled.div`
    width: 100%; 
    height: 10%;
`

const Middle = styled.div`
    width: 100%; 
    height: 80%;
`

const Middle_mid = styled.div`
    width: 70%; 
    height: 100%;
`

const Bottom = styled.div`
    width: 100%; 
    height: 10%;
    background:black;
`

const Container = styled.div`
  height:100vh;
  z-index:-1;
  @media (min-width: 768px){
    background-image: url('/images/Contact-us-m-big.png');
  }
  @media (max-width: 768px){
    background-image: url('/images/Contact-us-m-small1.png');
  }
  background-repeat: no-repeat;
  background-size: cover;
`

const Menu = styled.div`
font-weight: 600;
  @media(max-width:768px){
    display:none
  }
`

const CustomMenu = styled(MenuIcon)`
  cursor: pointer;
  margin-top: 10px;
`

const BurgerNav = styled.div`
  position:fixed;
  top:0;
  bottom:0;
  right:0;
  background: lime;
  width:300px;
  font-weight: 600;
  list-style:none;
  padding: 20px;
  display:flex;
  flex-direction: column;
  text-align: start;
  transform: ${props => props.show ? 'translateX(0)': 'translateX(100%)' };
  transition: transform 0.3s ease-in-out;
  li{
    padding 15px 0;
    border-bottom: 1px solid white;

  }
  z-index:1;
`

const CustomClose = styled(CloseIcon)`
  cursor:pointer;
`

const CustomWrapper = styled.div`
  display:flex;
  justify-content: flex-end;
`