import React from 'react';
import styled from 'styled-components';
import Fade from 'react-reveal/Fade';
import './Section.style.css';

function Section({title, description, leftBtnText, rightBtnText, backgroundImg}) {

    var audio1 = new Audio("/sound/sound-1-mild-boost.mp3")
    var audio2 = new Audio("/sound/sound-2.mp3")

    function sound1(){
        for(let i=1; i<=900000; i++){
        audio1.play();
        }
    }

    function sound2(){
        for(let i=1; i<=900000; i++){
        audio2.play();
        }
    }

  return (
    <Warp>
        <Fade bottom >
        <ItemText>
            <h1 id="w1">{title}</h1>
            <h2 id="w2">{description}</h2>
        </ItemText>
        </Fade>

        <Buttons>
            <Fade bottom>
            <ButtonGroup>
                <LeftButton>
                    <a onClick={sound1} href="/Ninja-ZX10R/view-main">{leftBtnText}</a>
                </LeftButton>
                { rightBtnText &&
                    <RightButton>
                        <a onClick={sound1} href="/Ninja-ZX10R/accessory-main">{rightBtnText}</a>
                    </RightButton>
                }
            </ButtonGroup>
            </Fade>
            <a onClick={sound2} href="/Ninja-650">
                <DownArrow src='/images/Down-Arrow-1.png'></DownArrow>
            </a>
        </Buttons>
        

    </Warp>
  );
}

export default Section;

//--Others--//

const Warp = styled.div`
    width: 100vw; 
    height: 100vh;
    background: black;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: centeral;
    display: flex;
    flex-direction: column;
    justify-content: space-between; // vertical 
    align-items:center;             //horizontal
    @media (min-width: 768px){
        background-image: url('/images/ZX10R-m-big.jpg') ;   
    }
    @media (max-width: 768px){
        background-image: url('/images/ZX10R-m-small.jpg') ;   
    }
`

const ItemText = styled.div`
    padding-top: 15vh;
    text-align: center;
`

const ButtonGroup  = styled.div`
    display: flex;
    margin-bottom: 30px;
    @media (max-width: 768px){
        flex-direction: column;
    }
`
const LeftButton  = styled.div`
    background-color: lime;
    color: white;
    height: 40px;
    width: 256px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius:100px;
    opacity: 85%;
    text-transform: uppercase;
    font-size:12px;
    cursor: pointer;
    margin: 8px;
    opacity: 0.85;
`

const RightButton  = styled(LeftButton)`
    background-color: white;
    color: black;
    opacity: 0.65;
`

const DownArrow = styled.img`
    height: 40px;
    overflow-x: hidden;
    animation: animateDown infinite 1.5s;
`

const Buttons = styled.div`

`