import React from 'react';
import styled from 'styled-components';
import './shop.css'
import { Scrollbars } from 'react-custom-scrollbars';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import {useState} from 'react';

function Shop() {

  const [burgerStatus, setBurgerStatus] = useState(false);

  return (
    <Container id ="cont-a">
      
        <Top id="top">
          <a>
            <img id="img" src='/images/Kawasaki-logo-2.png' alt=""/>
          </a>  
          
          <Menu>
            <a id="t-a"  href="/Ninja-300">Back</a>
            <a id="t-a"  href="/Contact-us">Contact</a>
          </Menu>

          <CustomMenu onClick={() => setBurgerStatus(true)}/>

          <BurgerNav show={burgerStatus}>
            <CustomWrapper>
              <CustomClose onClick={() => setBurgerStatus(false)}/>
            </CustomWrapper>
            <li><a id="t-a1"  href="/Ninja-300">Back</a></li>
            <li><a id="t-a1"  href="/Contact-us">Contact us</a></li>
          </BurgerNav>
        </Top>

      <Middle>
      <Scrollbars style={{ width: '100%', height: '100%' }}>
        <div id="div-a-m">

          <div id="div-a-sub">
              <img id="img-a-m" src="/images/N300-m-big.jpg"/>
              <h1 id="h1-a-1">Ninja 300 Accessory</h1>
              <a href="/Ninja-300/accessory-main">
                <button id="btn-1">View</button>
              </a>
          </div>

          <div id="div-a-sub">
              <img id="img-a-m" src="/images/H2R-m-big.jpg"/>
              <h1 id="h1-a-1">Ninja H2R Accessory</h1>
              <a href="/Ninja-H2R/accessory-main">
                <button id="btn-1">View</button>
              </a>
          </div>

          <div id="div-a-sub">
              <img id="img-a-m" src="/images/N650-m-big.jpg"/>
              <h1 id="h1-a-1">Ninja 650 Accessory</h1>
              <a href="/Ninja-650/accessory-main">
                <button id="btn-1">View</button>
              </a>
          </div>

          <div id="div-a-sub">
              <img id="img-a-m" src="/images/Z900-m-big.jpg"/>
              <h1 id="h1-a-1">Z900 Accessory</h1>
              <a href="/Ninja-Z900/accessory-main">
                <button id="btn-1">View</button>
              </a>
          </div>

          <div id="div-a-sub">
              <img id="img-a-m" src="/images/ZX10R-m-big.jpg"/>
              <h1 id="h1-a-1">ZX10R Accessory</h1>
              <a href="/Ninja-ZX10R/accessory-main">
                <button id="btn-1">View</button>
              </a>
          </div>

          <div id="div-a-sub">
              <img id="img-a-m" src="/images/RG-m-big.jpg"/>
              <h1 id="h1-a-1">Riding Gears</h1>
              <a href="/Riding-Gears/clothing-main">
                <button id="btn-1">View</button>
              </a>
          </div>

          <div id="div-a-sub">
              <img id="img-a-m" src="/images/SPORT-m-big.jpg"/>
              <h1 id="h1-a-1">Sports - Clothing</h1>
              <a href="/Shop/sports-clothing">
                <button id="btn-1">View</button>
              </a>
          </div>

          <div id="div-a-sub">
              <img id="img-a-m" src="/images/WSBK-m-big.jpg"/>
              <h1 id="h1-a-1">KRT World SBK - Clothing</h1>
              <a href="/Shop/wsbk-clothing">
                <button id="btn-1">View</button>
              </a>
          </div>

          <div id="div-a-sub">
              <img id="img-a-m" src="/images/TS-m-big.jpg"/>
              <h1 id="h1-a-1">T-Shirts</h1>
              <a href="/Shop/t-shirts-clothing">
                <button id="btn-1">View</button>
              </a>
          </div>

        </div>
      </Scrollbars>
      </Middle>

    </Container>
  );
}

export default Shop;



//--Others--//

const Top = styled.div`
    width: 100%; 
    height: 10%;
    background: brown;
`

const Middle = styled.div`
    width: 100%; 
    height: 90%;
`

const Container = styled.div`
  height:100vh;
  z-index:-1;
`

const Menu = styled.div`
font-weight: 600;
  @media(max-width:768px){
    display:none
  }
`

const CustomMenu = styled(MenuIcon)`
  cursor: pointer;
  margin-top: 10px;
`

const BurgerNav = styled.div`
  position:fixed;
  top:0;
  bottom:0;
  right:0;
  background: lime;
  width:300px;
  font-weight: 600;
  list-style:none;
  padding: 20px;
  display:flex;
  flex-direction: column;
  text-align: start;
  transform: ${props => props.show ? 'translateX(0)': 'translateX(100%)' };
  transition: transform 0.3s ease-in-out;
  li{
    padding 15px 0;
    border-bottom: 1px solid white;

  }
  z-index:1;
`

const CustomClose = styled(CloseIcon)`
  cursor:pointer;
`

const CustomWrapper = styled.div`
  display:flex;
  justify-content: flex-end;
`